

.center__container{
    .home__container{
        padding-top: 5px;
        margin-right: -300px;
        margin-bottom: 15px;
        float: left;
        width: 100%;
        z-index: 5;
        @media (max-width:900px) {
            margin: 0px 0px 15px 0px;
        }
        .home__containt{
            background-color: #fff;
            display: flex;
            flex-direction: column;
            gap: 50px;
            margin-right: 315px;
            @media (max-width:900px) {
                margin-right: 0px;
            }
            .home__header{
                padding: 10px 0 20px 0px;
                font-size: 28px;
                color: #000;
                font-weight: 700;
                font-family: "Oswald", sans-serif;
                span{
                    color: #ee3942
                }
                @media (max-width:500px) {
                    font-size: 25px;
                }
            }
            .home__slider{
                    display: grid; 
                    grid-template-rows: 200px 150px 200px 200px;
                    grid-template-columns: 1fr 1fr 1fr 1fr;
                    gap: 10px;
                    height: 100%;
                    @media (max-width:720px) {
                        grid-template-columns: 200px 200px 200px 200px 200px;
                        grid-template-rows: 200px 200px;
                        overflow-x: scroll;
                    }
                    img{
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        z-index: 1;
                        border-radius: 10px;
                        transition: .2s;
                        &:hover{
                            filter: brightness(60%);
                        }
                    }
                    h3{
                        width: 100%;
                        color: #fff;
                        z-index: 2;
                        font-size: 22px;
                        position: absolute;
                        left: 50%;
                        transform: translate(-50%, 0);
                        bottom: 5px;
                        text-align: center;
                        font-family: "Oswald", sans-serif;
                        line-height: 100%;
                        padding: 0 10px;
                    }
                    
                 .home__slider_1 {
                    position: relative;
                    grid-row-start: 1;
                    grid-column-start: 1;
                    grid-row-end: 2;
                    grid-column-end: 2;
                    @media (max-width:720px) {
                        grid-area: 1/1/2/2;
                    }
                 }
                 .home__slider_2 {
                    position: relative;

                    grid-row-start: 1;
                    grid-column-start: 2;
                    grid-row-end: 2;
                    grid-column-end: 3;
                    @media (max-width:720px) {
                        grid-area: 1/2/2/3;
                    }
                 }
                 .home__slider_3 {
                    position: relative;

                    grid-row-start: 2;
                    grid-column-start: 1;
                    grid-row-end: 4;
                    grid-column-end: 3;
                    @media (max-width:720px) {
                        grid-area: 1/3/2/4;
                    }
                 }
                 .home__slider_4 {
                    position: relative;

                    grid-row-start: 4;
                    grid-column-start: 1;
                    grid-row-end: 5;
                    grid-column-end: 3;
                    @media (max-width:720px) {
                        grid-area: 1/4/2/5;
                    }
                 }
                 .home__slider_5{
                    position: relative;

                    grid-row-start: 1;
                    grid-column-start: 3;
                    grid-row-end: 3;
                    grid-column-end: 4;
                    @media (max-width:720px) {
                        grid-area: 1/5/2/6;
                    }
                 }
                 .home__slider_6{
                    position: relative;

                    grid-row-start: 1;
                    grid-column-start: 4;
                    grid-row-end: 2;
                    grid-column-end: 5;
                    @media (max-width:720px) {
                        grid-area: 2/1/3/2;
                    }
                 }
                 .home__slider_7 {
                    position: relative;

                    grid-row-start: 2;
                    grid-column-start: 4;
                    grid-row-end: 4;
                    grid-column-end: 5;
                    @media (max-width:720px) {
                        grid-area: 2/2/3/3;
                    }
                 }
                 .home__slider_8 {
                    position: relative;

                    grid-row-start: 3;
                    grid-column-start: 3;
                    grid-row-end: 4;
                    grid-column-end: 4;
                    @media (max-width:720px) {
                        grid-area: 2/3/3/4;
                    }
                 }
                 .home__slider_9 {
                    position: relative;

                    grid-row-start: 4;
                    grid-column-start: 3;
                    grid-row-end: 5;
                    grid-column-end: 5;
                    @media (max-width:720px) {
                        grid-area: 2 / 4 / 3 / 6;
                    }
                }
                
            }
        }
    }
    .aside__container{
        float: right;
        width: 300px;
        padding: 5px;
        background-color: #fff;
    }
}