
.choice__item{
    position: relative;
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px;
    overflow: hidden;
    h3{
        position: absolute;
        left: 50%;
        bottom: 9%;
        width: 100%;
        transform: translate(-50%);
        font-size: 30px;
        color: #ffffff;
        font-family: "Oswald", sans-serif;
        text-align: center;
        line-height: 100%;
    } 
}